import { override } from '@microsoft/decorators';
import { Log } from '@microsoft/sp-core-library';
import {
  BaseApplicationCustomizer
} from '@microsoft/sp-application-base';
import { Dialog } from '@microsoft/sp-dialog';

import * as strings from 'TbfApplicationCustomizerStrings';

const LOG_SOURCE: string = 'TbfApplicationCustomizer';

/**
 * If your command set uses the ClientSideComponentProperties JSON input,
 * it will be deserialized into the BaseExtension.properties object.
 * You can define an interface to describe it.
 */
export interface ITbfApplicationCustomizerProperties {
  // This is an example; replace with your own property
  testMessage: string;
}

/** A Custom Action which can be run during execution of a Client Side Application */
export default class TbfApplicationCustomizer
  extends BaseApplicationCustomizer<ITbfApplicationCustomizerProperties> {

  @override
  public onInit(): Promise<void> {
    
    var style = document.createElement('style');
    style.innerHTML =
    `
    [class*=o365sx-navbar], [class*=searchResultsContainer], [class*=sp-App-bodyMain], [class*=SPPageChrome-app]>div {
      max-width: 1440px!important;
      margin: 0 auto;
      width: 100%;
    }
    [aria-label="Go to SharePoint"]{display:none !important}
    [id*=O365_AppName] {
      display: none!important;
    }
    [class*=o365sx-navbar] [class*=o365cs-base] [class*=o365sx-button], [class*=o365sx-navbar] [class*=o365cs-base][class*=o365sx-waffle], [class*=o365sx-navbar] [class*=o365cs-base] [class*=o365sx-waffle], [class*=o365sx-navbar] [class*=o365sx-button], [class*=o365sx-navbar] [class*=o365sx-waffle] {
      background-color: #222!important;
    }
    [class*=o365sx-navbar] {
      background-color: #222!important;
    }
    [class*=od-SuiteNav], [id*=SuiteNavPlaceHolder] {
      background-color: #212121!important;
    }
    `;
    var ref = document.querySelector('script');
    ref.parentNode.insertBefore(style, ref);

    return Promise.resolve();
  }
}



